import React from 'react';
import PropTypes from 'prop-types';
import {Provider} from 'react-redux';
import App from '../App';

const Root = ({store}) => {
	return (
		<Provider store={store}>
			<div>
				<App />
			</div>
		</Provider>
	);
};

Root.propTypes = {
    store: PropTypes.object.isRequired,
};

export default Root;



